import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import createPersistedState from 'vuex-persistedstate';

Vue.use(Vuex);
// 参与本地存储
const includeStorageState = [
  'token',
  'userinfo',
];
const store = new Vuex.Store({
  // strict: process.env.NODE_ENV !== 'production',
  strict: false,
  plugins: [createPersistedState({
    setState: (key, state, storage) => {
      const saved = {};
      Object.keys(state).filter(s => includeStorageState.includes(s)).forEach((k) => {
        saved[k] = state[k];
      });
      return storage.setItem(key, JSON.stringify(saved));
    },
  })],
  state: {
    token: '', // 用户凭证
    userinfo: {}, // 用户信息
    companyNatureList: [],
    companyScaleList: [],
    showLogin: false,
    showRegister: false,
    showUserinfo: false,
    showMenu: false,
    isEditInfo: false,
  },
  getters: {
    dictCompanyNature: state => (id) => {
      const items = state.companyNatureList.find(item => item.id === id);
      if (items) return items.value;
      return '';
    },
    dictCompanyScale: state => (id) => {
      const items = state.companyScaleList.find(item => item.id === id);
      if (items) return items.value;
      return '';
    },
  },
  mutations: {
    set(state, payload) {
      Object.keys(payload).forEach((k) => {
        Vue.set(state, k, payload[k]);
      });
    },
    toggleMenu(state) {
      state.showMenu = !state.showMenu;
      if (state.showMenu) {
        state.showLogin = false;
        state.showRegister = false;
        state.showUserinfo = false;
      }
    },
    closeAll() {
      this.state.showLogin = false;
      this.state.showRegister = false;
      this.state.showUserinfo = false;
      this.state.showMenu = false;
      this.state.isEditInfo = false;
    },
  },
  actions: {
    async fetchDict({ state, commit }, type = 'company_nature') {
      if (type === 'company_nature' && state.companyNatureList.length) return;
      if (type === 'company_scale' && state.companyScaleList.length) return;
      const { data } = await axios.post('/hrDict/list', {
        type,
      });
      if (data.status === 0) {
        if (type === 'company_nature') {
          commit('set', { companyNatureList: data.data });
        } else {
          commit('set', { companyScaleList: data.data });
        }
      }
    },
    login({ state, commit }) {
      if (
        state.showLogin || state.showRegister || state.showUserinfo
      ) {
        commit('set', {
          showLogin: false,
          showRegister: false,
          showUserinfo: false,
        });
        return;
      }
      const isLogin = !!state.token;
      if (isLogin) {
        commit('set', { showUserinfo: !state.showUserinfo });
      } else if (state.showLogin) {
        commit('set', { showLogin: false });
        commit('set', { showRegister: false });
      } else {
        commit('set', { showLogin: true });
      }
      commit('set', { showMenu: false });
    },
  },
});
export default store;
