<template>
  <div class="mod-dialog userinfo">
    <div class="mod-dialog-mask" @click="$store.commit('closeAll')"></div>
    <div class="content">
      <p>
        {{userinfo.tenantName || '[企业名称]'}}
      </p>
      <p>
        {{companyNatureName}} I {{companyScaleName}}
      </p>
      <div class="line"></div>
      <div class="item">
        {{userinfo.userName || '[用户名]'}}
      </div>
      <div class="item">
        电话：{{userinfo.mobile || '[手机号码]'}}
      </div>
    </div>
    <div class="handler">
      <a href="javascript:;" @click="onEdit"><img src="../assets/icon/icon_edit@2x.png"></a>
      <a href="javascript:;" @click="onLogout"><img src="../assets/icon/icon_sign@2x.png"></a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Userinfo',
  computed: {
    userinfo() {
      return this.$store.state.userinfo;
    },
    companyNatureName() {
      return this.$store.getters.dictCompanyNature(this.userinfo.companyNature) || '[企业性质]';
    },
    companyScaleName() {
      return this.$store.getters.dictCompanyScale(this.userinfo.companyScale) || '[企业规模]';
    },
  },
  created() {
    this.$store.dispatch('fetchDict', 'company_nature');
    this.$store.dispatch('fetchDict', 'company_scale');
  },
  methods: {
    onEdit() {
      this.$store.commit('set', {
        showRegister: true,
        showUserinfo: false,
        isEditInfo: true,
      });
    },
    onLogout() {
      this.$store.commit('set', {
        userinfo: null,
        token: null,
        showLogin: true,
        showUserinfo: false,
      });
    },
  },
};
</script>

