<template>
  <div class="mod-dialog register flex-column">
    <div class="mod-dialog-mask" @click="$store.commit('closeAll')"></div>
    <div class="dh">
      <h2 class="mod-title">{{isEditInfo?'修改信息':'注册'}}</h2>
      <!-- <h2 class="mod-title">请完善注册信息</h2> -->
    </div>
    <div class="form">
      <div class="form-item" :class="[error.userName.type]">
        <input type="text" v-model="formdata.userName" placeholder="账户使用者姓名">
        <div class="tip-info">{{error.userName.message}}</div>
        <i class="tip-dot"></i>
      </div>
      <div class="form-item" :class="[error.tenantName.type]">
        <input type="text" v-model="formdata.tenantName" placeholder="企业名称">
        <div class="tip-info">{{error.tenantName.message}}</div>
        <i class="tip-dot"></i>
      </div>
      <div class="form-item select" :class="[error.companyScale.type]">
        <span v-if="formdata.companyScale" class="select-text">
          {{companyScaleName}}
        </span>
        <div v-else class="placeholder">企业规模</div>
        <select v-model="formdata.companyScale">
          <option v-for="(option) in companyScaleList"
            :key="option.id" :value="option.id">{{option.value}}</option>
        </select>
        <div class="tip-info">{{error.companyScale.message}}</div>
        <i class="icon-arrow"></i>
      </div>
      <div class="form-item select" :class="[error.companyNature.type]">
        <span v-if="formdata.companyNature" class="select-text">
          {{companyNatureName}}
        </span>
        <div v-else class="placeholder">企业性质</div>
        <select v-model="formdata.companyNature">
          <option v-for="(option) in companyNatureList"
            :key="option.id" :value="option.id">{{option.value}}</option>
        </select>
        <div class="tip-info">{{error.companyNature.message}}</div>
        <i class="icon-arrow"></i>
      </div>
      <div v-if="!isEditInfo" class="form-item" :class="[error.mobile.type]">
        <input type="text" v-model="formdata.mobile" placeholder="请输入手机号码">
        <div class="tip-info">{{error.mobile.message}}</div>
        <i class="tip-dot"></i>
      </div>
      <div v-if="!isEditInfo" class="form-item verifycode" :class="[error.smsCode.type]">
        <input type="text" v-model="formdata.smsCode" placeholder="请输入短信验证码">
        <a class="verifycode-btn active" href="javascript:;"
          v-if="!verifycodeRepeat" @click="onFetchVerifycode">获取验证码</a>
        <a v-else class="verifycode-btn"
          href="javascript:;">重新获取({{verifycodeRepeatDuration}}s)</a>
        <div class="tip-info">{{error.smsCode.message}}</div>
        <i class="tip-dot"></i>
      </div>
      <div v-if="!isEditInfo" class="agreement" @click="onAgreement"
        :class="{active: formdata.agreement}">
        我接受<a href="javascript:;">用户协议</a>
        <div class="tip-info" v-if="error.agreement.message">{{error.agreement.message}}</div>
      </div>
      <p v-if="errorMessage" class="message">{{errorMessage}}</p>
      <button class="form-item btn" :disabled="isSubmit" @click="submit">
        <img src="../assets/icon/loading.svg">
        <span>{{isEditInfo?'修改':'注册'}}</span>
      </button>
      <a class="noaccount" href="javascript:;"
        v-if="!isEditInfo"
        @click="$store.commit('set', {showRegister: false })">已有账号？立即登录</a>
    </div>
  </div>
</template>

<script>
import Validator from '../validator';
import mixin from './smsCode.mixin';

export default {
  name: 'Register',
  mixins: [mixin],
  data() {
    const formdata = {
      userName: '',
      tenantName: '',
      companyScale: '',
      companyNature: '',
      mobile: '',
      smsCode: '',
      agreement: false,
    };
    const error = {};
    Object.keys(formdata).forEach((name) => {
      error[name] = { type: '', message: '' };
    });
    return {
      formdata,
      error,
      isSubmit: false,
      errorMessage: '',
      rules: {
        userName: [{
          required: true,
          message: '请输入账户使用者姓名',
          trigger: 'blur',
        }],
        tenantName: [{
          required: true,
          message: '请输入企业名称',
          trigger: 'blur',
        }],
        agreement: [{
          required: true,
          message: '请勾选用户协议',
          trigger: 'blur',
        }],
        companyScale: [{
          required: true,
          message: '请选择企业规模',
          trigger: 'blur',
        }],
        companyNature: [{
          required: true,
          message: '请选择企业名称',
          trigger: 'blur',
        }],
        mobile: [{
          required: true,
          message: '请输入手机号码',
          trigger: 'blur',
        }, {
          message: '手机格式不正确',
          pattern: /^1[3456789]\d{9}$/,
          trigger: 'blur',
        }],
        smsCode: [{
          required: true,
          message: '请输入验证码',
          trigger: 'blur',
        }, {
          len: 6,
          message: '验证码长度为6位',
          trigger: 'blur',
        }],
      },
    };
  },
  watch: {
    isEditInfo: {
      handler(val) {
        if (val) {
          this.formdata = this.$store.state.userinfo;
          delete this.rules.smsCode;
          delete this.rules.mobile;
          delete this.rules.agreement;
        }
      },
      immediate: true,
    },
  },
  computed: {
    companyNatureList() {
      return this.$store.state.companyNatureList || [];
    },
    companyScaleList() {
      return this.$store.state.companyScaleList || [];
    },
    companyNatureName() {
      return this.$store.getters.dictCompanyNature(this.formdata.companyNature);
    },
    companyScaleName() {
      return this.$store.getters.dictCompanyScale(this.formdata.companyScale);
    },
    isEditInfo() {
      return this.$store.state.isEditInfo;
    },
  },
  created() {
    this.$store.dispatch('fetchDict', 'company_nature');
    this.$store.dispatch('fetchDict', 'company_scale');
  },
  mounted() {},
  methods: {
    submit() {
      const res = Validator(this.formdata, this.rules);
      this.error = res.series;
      if (res.pass) {
        this.isSubmit = true;
        this.errorMessage = '';
        if (this.isEditInfo) {
          this.update();
        } else {
          this.register();
        }
      }
    },
    async register() {
      try {
        const { data } = await this.$http.post('/hrUser/register', this.formdata);
        if (data.status !== 0) {
          this.errorMessage = data.message;
          // this.$store.commit('set', {
          //   userinfo: this.formdata,
          //   token: 'x',
          //   showUserinfo: true,
          //   showLogin: false,
          //   showRegister: false,
          // });
        } else {
          this.$store.commit('set', {
            userinfo: this.formdata,
            token: data.data.token,
            showUserinfo: true,
            showLogin: false,
            showRegister: false,
          });
        }
      } catch (e) {
        console.error(e);
      } finally {
        this.resetRepeatTimer();
        this.isSubmit = false;
      }
    },
    async update() {
      try {
        const { data } = await this.$http.post('/hrUser/update', this.formdata);
        if (data.status !== 0) {
          this.errorMessage = data.message;
          this.$store.commit('set', {
            userinfo: this.formdata,
            showUserinfo: true,
            showLogin: false,
            showRegister: false,
          });
        } else {
          this.$store.commit('set', {
            userinfo: this.formdata,
            showUserinfo: true,
            showLogin: false,
            showRegister: false,
          });
        }
      } catch (e) {
        this.errorMessage = '请求失败！';
        console.error(e);
      } finally {
        this.isSubmit = false;
      }
    },
    onAgreement() {
      this.formdata.agreement = !this.formdata.agreement;
      this.error.agreement = { type: '', message: '' };
    },
  },
};
</script>

<style lang="scss" scoped>
.mod{
  background-image: url(../assets/bg/home_1920.png);
}
.dh{
  position: relative;
  margin-bottom: 85px;
}

.noaccount{
  font-size:14px;
  font-weight:800;
  color:rgba(255,255,255,1);
  line-height:22px;
  text-align: center;
  display: block;
  margin-top: 40px;
  text-decoration: none;
}
.agreement{
  color: #FFF;
  padding-left: 27px;
  line-height: 18px;
  position: relative;
  margin: 30px 0 25px;
  &::before, &::after{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    border: 2px solid #FFF;
    display: block;
  }
  &::after{
    left: 4px;
    top: 4px;
    width: 8px;
    height: 8px;
    transition: transform 0.3s;
    background: #FFF;
    border: none;
    transform: scale(0);
  }
  &.active:after{
    transform: scale(1);
  }
  a{
    color: #FFF;
  }
}
.message{
  text-align: center;
  padding: 10px 0;
  color: #e35711;
  font-size: 14px;
}
@import '../styles/mixin';
@include respond-to(wide) {
  .dh {
    margin-bottom: 20px;
  }
  .agreement{
    margin: 10px 0;
  }
  .noaccount{
    margin-top: 20px;
  }
}
@include respond-to(mobile) {
  .mod-dialog{
    justify-content: flex-start;
    height: calc(100vh - 60px);
    overflow-y: auto;
  }
}
</style>
